@import "https://fonts.googleapis.com/css2?family=Poppins&display=swap";
.hero-bg {
  background-position: center;
  background-size: cover;
  place-items: center;
  height: 550px;
  display: grid;
  position: relative;
}

.hero-bg .container {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}

.hero-bg .text-wrapper {
  position: relative;
}

.hero-bg .text-wrapper h2 {
  color: #fff;
  text-align: center;
  font-size: 31px;
  font-weight: bold;
}

.hero-bg .text-wrapper ul {
  flex-wrap: wrap;
  justify-content: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.hero-bg .text-wrapper ul li {
  color: #fff;
  text-transform: uppercase;
  margin: 0 6px;
}

.hero-bg:before {
  content: "";
  background-color: #29345c9e;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

@media screen and (width <= 767px) {
  .nav-link {
    color: #000;
  }

  .hero-bg {
    height: 400px;
  }

  .hero-bg img {
    background-size: cover;
  }

  .hero-bg:before {
    background-color: #29345ccc;
  }
}

.form-container {
  margin-bottom: 40px;
  margin-top: 100px !important;
}

.form-col {
  border-radius: 10px;
  padding: 30px;
  box-shadow: 0 10px 15px -3px #0000001a, 0 4px 6px -2px #0000000d;
}

.form-col h4, .register-form h4 {
  color: #7744d9;
}

label {
  color: var(--title-color);
  letter-spacing: -.011em;
  width: 100%;
  margin-top: 1rem;
  margin-bottom: .65rem;
  margin-right: 1rem;
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
}

.input, select, option {
  color: #333;
  border: 1px solid #e4e5e7;
  border-radius: 4px;
  width: 100%;
  padding: 4px 10px;
  font-size: 14px;
  transition: all .7s;
}

.register-form {
  border-radius: 10px;
  padding: 30px;
  box-shadow: 0 10px 15px -3px #0000001a, 0 4px 6px -2px #0000000d;
}

.register-form form .input, select, .register-form form div {
  width: 100% !important;
}

select option {
  border: 1px solid #fff;
}

select option:focus {
  border: 1px solid #e4e5e7;
}

.input:focus {
  border: 1px solid #7744d9;
  outline: none !important;
}

.input:hover {
  border: 1px solid #7744d9;
}

.form-btn {
  color: #fff;
  cursor: pointer;
  background-color: #7744d9;
  border: 0;
  border-radius: 6px;
  outline: none;
  margin-top: 20px;
  padding: 6px 28px;
  font-size: 14px;
  font-weight: 700;
  text-decoration: none;
  transition: all .3s;
  box-shadow: 0 0 #00000029;
}

.forgot-password {
  color: #7744d9;
  margin-top: 20px;
  margin-left: 10px;
  font-size: 14px;
  font-weight: bold;
  text-decoration: underline;
}

.register {
  color: #7a7a7a;
  -webkit-transition: all .3s;
  border: none;
  font-size: 15px;
  text-decoration: none;
  transition: all 1s;
  display: inline-block;
  position: relative;
}

.register:after {
  content: "";
  -webkit-transition: all .3s;
  background-color: #0000004d;
  width: 0%;
  height: 2px;
  transition: all .7s;
  position: absolute;
  bottom: -5px;
  left: 0;
}

.register:hover {
  color: #000;
}

.register:hover:after {
  background-color: #7744d9;
  width: 100%;
}

@media screen and (width <= 767px) {
  .form-col, .register-form {
    width: 90% !important;
  }

  .form-div {
    flex-wrap: wrap;
    gap: 0 !important;
  }
}

.input[type="number"]::-webkit-inner-spin-button {
  appearance: none;
  margin: 0;
}

.input[type="number"]::-webkit-outer-spin-button {
  appearance: none;
  margin: 0;
}

.error-message {
  color: red;
}
